import React from "react";

function ViewControls({
  sortOption,
  setSortOption,
  sortEnhancement,
  setSortEnhancement,
  showDescription,
  setShowDescription,
  showUngrouped,
  setShowUngrouped,
  globalEnhancement,
  setGlobalEnhancement,
  hideDeleted,
  setHideDeleted,
}) {
  return (
    <div className="view-controls">
      <h1>  </h1>
      <div className="controls-group">
        <div className="sort-controls">
          <label htmlFor="sort-select">:</label>
          <select
            id="sort-select"
            value={sortOption}
            onChange={(e) => setSortOption(e.target.value)}
          >
            <option value=""></option>
            <option value=" "> </option>
            <option value="DPS">DPS</option>
          </select>
          <label htmlFor="enhancement-select"> :</label>
          <select
            id="enhancement-select"
            value={sortEnhancement}
            onChange={(e) => setSortEnhancement(Number(e.target.value))}
          >
            {[...Array(16).keys()].map((level) => (
              <option key={level} value={level}>
                +{level}
              </option>
            ))}
          </select>
          <label htmlFor="global-enhancement-select"> :</label>
          <select
            id="global-enhancement-select"
            value={globalEnhancement}
            onChange={(e) => {
              const value = e.target.value;
              setGlobalEnhancement(value === "" ? "" : Number(value));
            }}
          >
            <option value=""> </option>
            {[...Array(16).keys()].map((level) => (
              <option key={level} value={level}>
                +{level}
              </option>
            ))}
          </select>
        </div>
        <label>
          <input
            type="checkbox"
            checked={showDescription}
            onChange={(e) => setShowDescription(e.target.checked)}
          />
           
        </label>
      </div>
      <div className="controls-group">
        <label>
          <input
            type="checkbox"
            checked={!showUngrouped}
            onChange={(e) => setShowUngrouped(!e.target.checked)}
          />
           
        </label>
        <label>
          <input
            type="checkbox"
            checked={hideDeleted}
            onChange={(e) => setHideDeleted(e.target.checked)}
          />
            
        </label>
      </div>
    </div>
  );
}

export default ViewControls;
